body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #eff3f7;
}

html,
body,
#potato
.container{
    height: 100%;
    display: flex;
    justify-content: center;
}

*{
    box-sizing: border-box;
}

.loader{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.movies{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    padding-top: 70px;
    width: 80%;
}

.movies .movie{
    width: 45%;
    margin-bottom: 70px;
    background-color: white;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    position: relative;
    padding: 20px;
    color: #b2b3bc;
    border-radius: 5px;

}

.movie img {
    position: relative;
    top: -50px;
    max-width: 100px;
    width: 100%;
    margin-right: 30px;
}

.movie .movie__title,
.movie .movie__year {
    margin: 0;
    font-weight: 300px;
}

.movie .movie__title{
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
}

.movie .movie__genres{
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    margin: 5px 0px;
}

.movie__genres li{
    margin-right: 10px;

}